import React from 'react';
import { LeagueConsumer } from './index.js';
import LeagueFrame from './frame.js';

const LeagueFrameContainer = (props) => {
	return <LeagueConsumer>
		{(context) => (
			createFrames(context.noOfFrames, context.targetFrame)
		)}
	</LeagueConsumer>
};

const createFrames = (noOfFrames, targetFrame) => {
	const start = targetFrame.frame < 3 ? 0 : targetFrame.frame - 2;
	const limit = targetFrame.frame + 1;

	let frames = [];

	for (let i = start; i < limit; i++) {
		frames.push(<LeagueFrame key={i} frameNo={i} />)
	}

	return frames;
};

export default LeagueFrameContainer;
